// src/lib/sentry.ts

// Temporary no-op Sentry shim to avoid module errors during dev
const Sentry = null as any;

export const sentry = {
  captureException: (..._args: any[]) => {},
  captureMessage: (..._args: any[]) => {},
  withScope: (fn: (scope: any) => void) => fn({}),
};

// No-op functions for compatibility
export function initSentry() {}
export function setSentryUser(_userId?: string, _apiKeyId?: string) {}
export function setSentryContext(_context: any) {}
export function captureException(_error: Error, _context?: any) {}
export function captureMessage(_message: string, _level: any = 'info', _context?: any) {}
