// =============================================================================
// CRON JOBS API ENDPOINT
// =============================================================================
// GET /api/v1/cron-jobs
//
// Returns cron job configurations and recent run history for monitoring.
// Requires admin API key for access.

import { NextRequest, NextResponse } from 'next/server';
import { createLogger } from '@/lib/logger';
import { authenticateRequest } from '@/lib/auth';
import {
  getAllJobConfigs,
  getAllRecentRuns,
  getRecentJobRuns,
  getFailingJobs,
  type CronJobConfig,
  type CronJobRun,
} from '@/db/cron-jobs';

const logger = createLogger({ component: 'CronJobsAPI' });

// =============================================================================
// GET - List all cron jobs with status
// =============================================================================

export async function GET(request: NextRequest): Promise<NextResponse> {
  const requestId = crypto.randomUUID();

  try {
    // Validate API key (allow any authenticated user to view)
    const authResult = await authenticateRequest(request);
    if ('error' in authResult) {
      return NextResponse.json(
        { success: false, error: authResult.error, code: 'UNAUTHORIZED' },
        { status: authResult.status }
      );
    }

    // Parse query params
    const { searchParams } = new URL(request.url);
    const jobId = searchParams.get('jobId');
    const limit = parseInt(searchParams.get('limit') || '50', 10);
    const includeRuns = searchParams.get('includeRuns') !== 'false';
    const onlyFailing = searchParams.get('onlyFailing') === 'true';

    logger.info({ requestId, jobId, limit, includeRuns, onlyFailing }, 'Fetching cron jobs');

    // Get job configurations
    let configs: CronJobConfig[] = [];
    try {
      configs = await getAllJobConfigs();
    } catch (error) {
      logger.warn({ requestId, error }, 'Failed to fetch job configs');
      // Continue with empty configs
    }

    // Get failing jobs if requested
    let failingJobs: CronJobConfig[] = [];
    if (onlyFailing) {
      try {
        failingJobs = await getFailingJobs(3);
      } catch (error) {
        logger.warn({ requestId, error }, 'Failed to fetch failing jobs');
      }
    }

    // Get recent runs
    let runs: CronJobRun[] = [];
    if (includeRuns) {
      try {
        if (jobId) {
          runs = await getRecentJobRuns(jobId, limit);
        } else {
          runs = await getAllRecentRuns(limit);
        }
      } catch (error) {
        logger.warn({ requestId, error }, 'Failed to fetch job runs');
      }
    }

    // Build job summaries with computed stats
    const jobSummaries = configs.map((config) => {
      const jobRuns = runs.filter((r) => r.job_id === config.job_id);
      const completedRuns = jobRuns.filter((r) => r.status === 'completed');
      const failedRuns = jobRuns.filter((r) => r.status === 'failed' || r.status === 'timeout');

      // Calculate success rate
      const totalRuns = jobRuns.length;
      const successRate = totalRuns > 0 ? completedRuns.length / totalRuns : null;

      // Calculate average duration from completed runs
      const durations = completedRuns
        .map((r) => r.duration_ms)
        .filter((d): d is number => d !== undefined && d !== null);
      const avgDuration = durations.length > 0
        ? Math.round(durations.reduce((a, b) => a + b, 0) / durations.length)
        : null;

      // Get last run
      const lastRun = jobRuns[0] || null;

      return {
        jobId: config.job_id,
        displayName: config.display_name,
        description: config.description,
        schedule: config.schedule,
        enabled: config.enabled,
        timeoutMs: config.timeout_ms,
        maxRetries: config.max_retries,
        alertOnFailure: config.alert_on_failure,
        consecutiveFailures: config.consecutive_failures,
        lastSuccessAt: config.last_success_at,
        lastFailureAt: config.last_failure_at,
        // Computed stats
        stats: {
          totalRuns,
          successRate,
          avgDurationMs: avgDuration,
          recentFailures: failedRuns.length,
        },
        lastRun: lastRun
          ? {
              id: lastRun.id,
              status: lastRun.status,
              startedAt: lastRun.started_at,
              completedAt: lastRun.completed_at,
              durationMs: lastRun.duration_ms,
              triggerSource: lastRun.trigger_source,
              errorMessage: lastRun.error_message,
            }
          : null,
      };
    });

    // Map runs for detailed view
    const recentRuns = runs.map((run) => ({
      id: run.id,
      jobId: run.job_id,
      status: run.status,
      startedAt: run.started_at,
      completedAt: run.completed_at,
      durationMs: run.duration_ms,
      triggerSource: run.trigger_source,
      attemptNumber: run.attempt_number,
      errorMessage: run.error_message,
      outputSummary: run.output_summary,
      metadata: run.metadata,
    }));

    // Summary stats
    const activeJobs = configs.filter((c) => c.enabled).length;
    const runningJobs = runs.filter((r) => r.status === 'running').length;
    const recentlyFailed = configs.filter((c) => c.consecutive_failures > 0).length;

    logger.info({
      requestId,
      jobCount: configs.length,
      runCount: runs.length,
      activeJobs,
      runningJobs,
      recentlyFailed,
    }, 'Cron jobs fetched successfully');

    return NextResponse.json({
      success: true,
      summary: {
        totalJobs: configs.length,
        activeJobs,
        runningJobs,
        recentlyFailed,
        failingJobsCount: failingJobs.length,
      },
      jobs: onlyFailing
        ? jobSummaries.filter((j) => j.consecutiveFailures >= 3)
        : jobSummaries,
      recentRuns: includeRuns ? recentRuns : undefined,
      failingJobs: onlyFailing
        ? failingJobs.map((j) => ({
            jobId: j.job_id,
            displayName: j.display_name,
            consecutiveFailures: j.consecutive_failures,
            lastFailureAt: j.last_failure_at,
          }))
        : undefined,
    });
  } catch (error) {
    const errorMsg = error instanceof Error ? error.message : 'Unknown error';
    logger.error({ requestId, error: errorMsg }, 'Failed to fetch cron jobs');

    return NextResponse.json(
      {
        success: false,
        error: 'Failed to fetch cron jobs',
        code: 'INTERNAL_ERROR',
        details: process.env.NODE_ENV === 'development' ? errorMsg : undefined,
      },
      { status: 500 }
    );
  }
}

// =============================================================================
// POST - Manually trigger a cron job
// =============================================================================

export async function POST(request: NextRequest): Promise<NextResponse> {
  const requestId = crypto.randomUUID();

  try {
    // Validate API key (require admin for triggering jobs)
    const authResult = await authenticateRequest(request);
    if ('error' in authResult) {
      return NextResponse.json(
        { success: false, error: authResult.error, code: 'UNAUTHORIZED' },
        { status: authResult.status }
      );
    }

    // Only admins can trigger jobs
    if (authResult.apiKey.role !== 'admin') {
      return NextResponse.json(
        { success: false, error: 'Admin access required', code: 'FORBIDDEN' },
        { status: 403 }
      );
    }

    const body = await request.json();
    const { jobId } = body;

    if (!jobId) {
      return NextResponse.json(
        { success: false, error: 'jobId is required', code: 'INVALID_INPUT' },
        { status: 400 }
      );
    }

    // Map job IDs to their cron endpoint paths
    const jobEndpoints: Record<string, string> = {
      'dogfood': '/api/cron/dogfood',
      'scout': '/api/cron/scout',
      'coord': '/api/cron/coord',
      'recommendation-applier': '/api/cron/recommendation-applier',
      'pull-metrics': '/api/cron/pull-metrics',
      'social-reply': '/api/cron/social-reply',
      'house-x402-job': '/api/cron/house-x402-job',
      'crawl-actions': '/api/cron/crawl-actions',
      'x402-discovery': '/api/cron/x402-discovery',
      'facilitator-probes': '/api/cron/facilitator-probes',
    };

    const endpoint = jobEndpoints[jobId];
    if (!endpoint) {
      return NextResponse.json(
        { success: false, error: `Unknown job: ${jobId}`, code: 'JOB_NOT_FOUND' },
        { status: 404 }
      );
    }

    // Trigger the job internally
    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'http://localhost:3000';
    const cronSecret = process.env.CRON_SECRET;

    logger.info({ requestId, jobId, endpoint }, 'Manually triggering cron job');

    const response = await fetch(`${baseUrl}${endpoint}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-trigger-source': 'manual',
        'x-attempt-number': '1',
        ...(cronSecret ? { 'Authorization': `Bearer ${cronSecret}` } : {}),
      },
    });

    const result = await response.json();

    return NextResponse.json({
      success: response.ok,
      jobId,
      triggeredAt: new Date().toISOString(),
      response: result,
    }, { status: response.ok ? 200 : 500 });
  } catch (error) {
    const errorMsg = error instanceof Error ? error.message : 'Unknown error';
    logger.error({ requestId, error: errorMsg }, 'Failed to trigger cron job');

    return NextResponse.json(
      {
        success: false,
        error: 'Failed to trigger job',
        code: 'INTERNAL_ERROR',
        details: process.env.NODE_ENV === 'development' ? errorMsg : undefined,
      },
      { status: 500 }
    );
  }
}

