// =============================================================================
// Structured Logging
// =============================================================================
// Uses Pino for fast, structured logging
// Supports request ID tracking and different log levels
// All logs include standard fields: timestamp, level, message, requestId, etc.

import pino from 'pino';

// Determine log level from environment
const logLevel = process.env.LOG_LEVEL || (process.env.NODE_ENV === 'production' ? 'info' : 'debug');

// Create logger instance with structured output
// In Next.js dev mode, disable pino-pretty transport to avoid worker thread issues
// Use simple JSON output instead (can be prettified by other tools)
const isNextDev = process.env.NODE_ENV === 'development' && process.env.NEXT_RUNTIME;
const usePrettyTransport = process.env.NODE_ENV !== 'production' && !isNextDev;

const logger = pino({
  level: logLevel,
  transport: usePrettyTransport ? {
    target: 'pino-pretty',
    options: {
      colorize: true,
      translateTime: 'SYS:standard',
      ignore: 'pid,hostname',
    },
  } : undefined,
  formatters: {
    level: (label) => {
      return { level: label.toUpperCase() };
    },
  },
  base: {
    env: process.env.NODE_ENV || 'development',
    service: 'nexflow-api',
  },
  // Ensure timestamps are included
  timestamp: pino.stdTimeFunctions.isoTime,
});

export type LogLevel = 'trace' | 'debug' | 'info' | 'warn' | 'error' | 'fatal';

/**
 * Standard log context fields
 */
export interface LogContext {
  // Request tracking
  requestId?: string;
  apiKeyId?: string;
  endpoint?: string;
  method?: string;
  statusCode?: number;
  durationMs?: number;
  ip?: string;
  
  // Error context
  errorCode?: string;
  error?: Error | any;
  stack?: string;
  
  // CDP/x402 context
  cdpStatus?: string;
  cdpErrorId?: string;
  paymentIntentId?: string;
  x402HeaderId?: string;
  
  // Rate limiting
  rateLimitKey?: string;
  rateLimitRemaining?: number;
  
  // Additional context
  [key: string]: any;
}

/**
 * Create a child logger with additional context
 * Useful for request-scoped logging with request IDs
 */
export function createLogger(context?: LogContext | Record<string, any>) {
  return context ? logger.child(context) : logger;
}

/**
 * Get the default logger instance
 */
export function getLogger() {
  return logger;
}

/**
 * Sanitize sensitive data from log context
 * Removes API keys, tokens, payment headers, and PII
 */
function sanitizeContext(context: LogContext): LogContext {
  const sanitized = { ...context };
  
  // Remove sensitive fields
  const sensitiveKeys = [
    'apiKey', 'token', 'authorization', 'x-payment', 'paymentHeader',
    'cardNumber', 'cvv', 'ssn', 'password', 'secret',
  ];
  
  sensitiveKeys.forEach(key => {
    if (key in sanitized) {
      delete sanitized[key];
    }
  });
  
  // Truncate long strings that might contain sensitive data
  Object.keys(sanitized).forEach(key => {
    const value = sanitized[key];
    if (typeof value === 'string' && value.length > 200) {
      sanitized[key] = value.substring(0, 200) + '... [truncated]';
    }
  });
  
  return sanitized;
}

/**
 * Log levels with structured context
 */
export const log = {
  trace: (msg: string, context?: LogContext, ...args: any[]) => {
    logger.trace(sanitizeContext(context || {}), msg, ...args);
  },
  debug: (msg: string, context?: LogContext, ...args: any[]) => {
    logger.debug(sanitizeContext(context || {}), msg, ...args);
  },
  info: (msg: string, context?: LogContext, ...args: any[]) => {
    logger.info(sanitizeContext(context || {}), msg, ...args);
  },
  warn: (msg: string, context?: LogContext, ...args: any[]) => {
    logger.warn(sanitizeContext(context || {}), msg, ...args);
  },
  error: (msg: string, context?: LogContext | Error, ...args: any[]) => {
    let logContext: LogContext = {};
    let errorObj: Error | undefined;
    
    if (context instanceof Error) {
      errorObj = context;
      logContext = {
        error: context,
        errorCode: (context as any).code || 'UNKNOWN_ERROR',
        stack: context.stack,
      };
    } else if (context && typeof context === 'object') {
      logContext = context as LogContext;
      if (logContext.error instanceof Error) {
        errorObj = logContext.error;
        logContext.stack = logContext.error.stack;
      }
    }
    
    const sanitizedContext = sanitizeContext(logContext);
    logger.error(sanitizedContext, msg, ...args);
    
    // Automatically capture to Sentry if error object exists
    if (errorObj) {
      try {
        const { captureException } = require('./sentry');
        captureException(errorObj, {
          requestId: logContext.requestId,
          apiKeyId: logContext.apiKeyId,
          endpoint: logContext.endpoint,
          endpointId: (logContext as any).endpointId,
          method: logContext.method,
          statusCode: logContext.statusCode,
          ip: logContext.ip,
          userAgent: (logContext as any).userAgent,
          tags: {
            errorCode: logContext.errorCode || 'UNKNOWN_ERROR',
            component: (logContext as any).component || 'unknown',
          },
          extra: {
            ...sanitizedContext,
          },
        });
      } catch (sentryError) {
        // Silently fail if Sentry not available
      }
    }
  },
  fatal: (msg: string, context?: LogContext | Error, ...args: any[]) => {
    let logContext: LogContext = {};
    let errorObj: Error | undefined;
    
    if (context instanceof Error) {
      errorObj = context;
      logContext = {
        error: context,
        errorCode: (context as any).code || 'UNKNOWN_ERROR',
        stack: context.stack,
      };
    } else if (context && typeof context === 'object') {
      logContext = context as LogContext;
      if (logContext.error instanceof Error) {
        errorObj = logContext.error;
        logContext.stack = logContext.error.stack;
      }
    }
    
    const sanitizedContext = sanitizeContext(logContext);
    logger.fatal(sanitizedContext, msg, ...args);
    
    // Automatically capture to Sentry for fatal errors
    if (errorObj) {
      try {
        const { captureException } = require('./sentry');
        captureException(errorObj, {
          requestId: logContext.requestId,
          apiKeyId: logContext.apiKeyId,
          endpoint: logContext.endpoint,
          endpointId: (logContext as any).endpointId,
          method: logContext.method,
          statusCode: logContext.statusCode,
          ip: logContext.ip,
          userAgent: (logContext as any).userAgent,
          tags: {
            errorCode: logContext.errorCode || 'FATAL_ERROR',
            component: (logContext as any).component || 'unknown',
            severity: 'fatal',
          },
          extra: {
            ...sanitizedContext,
          },
        });
      } catch (sentryError) {
        // Silently fail if Sentry not available
      }
    }
  },
};

export default logger;

