// =============================================================================
// CRON JOB DATABASE OPERATIONS
// =============================================================================
// Database operations for cron job tracking and configuration.

import { getDb } from './client';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'CronJobDb' });

// =============================================================================
// TYPES
// =============================================================================

export type CronJobStatus = 'pending' | 'running' | 'completed' | 'failed' | 'timeout';
export type TriggerSource = 'cron' | 'manual' | 'retry';

export interface CronJobRun {
  id: string;
  job_id: string;
  status: CronJobStatus;
  started_at: string;
  completed_at?: string;
  duration_ms?: number;
  trigger_source: TriggerSource;
  attempt_number: number;
  input_params?: Record<string, any>;
  output_summary?: Record<string, any>;
  error_message?: string;
  error_stack?: string;
  metadata?: Record<string, any>;
  created_at: string;
}

export interface CronJobConfig {
  job_id: string;
  display_name: string;
  description?: string;
  schedule?: string;
  enabled: boolean;
  timeout_ms: number;
  max_retries: number;
  retry_delay_ms: number;
  alert_on_failure: boolean;
  consecutive_failures: number;
  last_success_at?: string;
  last_failure_at?: string;
  created_at: string;
  updated_at: string;
}

export interface CreateJobRunInput {
  jobId: string;
  triggerSource?: TriggerSource;
  attemptNumber?: number;
  inputParams?: Record<string, any>;
  metadata?: Record<string, any>;
}

export interface CompleteJobRunInput {
  runId: string;
  status: 'completed' | 'failed' | 'timeout';
  outputSummary?: Record<string, any>;
  errorMessage?: string;
  errorStack?: string;
}

// =============================================================================
// JOB RUN OPERATIONS
// =============================================================================

/**
 * Create a new job run record
 */
export async function createJobRun(input: CreateJobRunInput): Promise<CronJobRun> {
  const db = getDb();
  const id = crypto.randomUUID();
  
  if ('pool' in db) {
    // PostgreSQL
    const result = await db.query(
      `INSERT INTO cron_job_runs (id, job_id, status, trigger_source, attempt_number, input_params, metadata)
       VALUES ($1, $2, 'running', $3, $4, $5, $6)
       RETURNING *`,
      [
        id,
        input.jobId,
        input.triggerSource || 'cron',
        input.attemptNumber || 1,
        input.inputParams ? JSON.stringify(input.inputParams) : null,
        input.metadata ? JSON.stringify(input.metadata) : null,
      ]
    );
    return result.rows[0];
  } else {
    // SQLite
    db.prepare(
      `INSERT INTO cron_job_runs (id, job_id, status, trigger_source, attempt_number, input_params, metadata, started_at, created_at)
       VALUES (?, ?, 'running', ?, ?, ?, ?, datetime('now'), datetime('now'))`
    ).run(
      id,
      input.jobId,
      input.triggerSource || 'cron',
      input.attemptNumber || 1,
      input.inputParams ? JSON.stringify(input.inputParams) : null,
      input.metadata ? JSON.stringify(input.metadata) : null
    );
    return db.prepare('SELECT * FROM cron_job_runs WHERE id = ?').get(id) as CronJobRun;
  }
}

/**
 * Complete a job run (success or failure)
 */
export async function completeJobRun(input: CompleteJobRunInput): Promise<void> {
  const db = getDb();
  
  if ('pool' in db) {
    await db.query(
      `UPDATE cron_job_runs
       SET status = $1,
           completed_at = now(),
           duration_ms = EXTRACT(EPOCH FROM (now() - started_at)) * 1000,
           output_summary = $2,
           error_message = $3,
           error_stack = $4
       WHERE id = $5`,
      [
        input.status,
        input.outputSummary ? JSON.stringify(input.outputSummary) : null,
        input.errorMessage || null,
        input.errorStack || null,
        input.runId,
      ]
    );
  } else {
    db.prepare(
      `UPDATE cron_job_runs
       SET status = ?,
           completed_at = datetime('now'),
           duration_ms = (julianday('now') - julianday(started_at)) * 86400000,
           output_summary = ?,
           error_message = ?,
           error_stack = ?
       WHERE id = ?`
    ).run(
      input.status,
      input.outputSummary ? JSON.stringify(input.outputSummary) : null,
      input.errorMessage || null,
      input.errorStack || null,
      input.runId
    );
  }

  // Update config stats
  await updateConfigStats(input);
}

/**
 * Update job config statistics after run completion
 */
async function updateConfigStats(input: CompleteJobRunInput): Promise<void> {
  const db = getDb();
  
  // Get the job_id from the run
  let jobId: string;
  if ('pool' in db) {
    const result = await db.query(
      'SELECT job_id FROM cron_job_runs WHERE id = $1',
      [input.runId]
    );
    if (result.rows.length === 0) return;
    jobId = result.rows[0].job_id;
  } else {
    const row = db.prepare('SELECT job_id FROM cron_job_runs WHERE id = ?').get(input.runId) as { job_id: string } | undefined;
    if (!row) return;
    jobId = row.job_id;
  }

  if (input.status === 'completed') {
    // Reset consecutive failures, update last success
    if ('pool' in db) {
      await db.query(
        `UPDATE cron_job_config
         SET consecutive_failures = 0, last_success_at = now(), updated_at = now()
         WHERE job_id = $1`,
        [jobId]
      );
    } else {
      db.prepare(
        `UPDATE cron_job_config
         SET consecutive_failures = 0, last_success_at = datetime('now'), updated_at = datetime('now')
         WHERE job_id = ?`
      ).run(jobId);
    }
  } else if (input.status === 'failed' || input.status === 'timeout') {
    // Increment consecutive failures, update last failure
    if ('pool' in db) {
      await db.query(
        `UPDATE cron_job_config
         SET consecutive_failures = consecutive_failures + 1, last_failure_at = now(), updated_at = now()
         WHERE job_id = $1`,
        [jobId]
      );
    } else {
      db.prepare(
        `UPDATE cron_job_config
         SET consecutive_failures = consecutive_failures + 1, last_failure_at = datetime('now'), updated_at = datetime('now')
         WHERE job_id = ?`
      ).run(jobId);
    }
  }
}

/**
 * Get recent runs for a job
 */
export async function getRecentJobRuns(
  jobId: string,
  limit = 20
): Promise<CronJobRun[]> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query(
      `SELECT * FROM cron_job_runs
       WHERE job_id = $1
       ORDER BY started_at DESC
       LIMIT $2`,
      [jobId, limit]
    );
    return result.rows;
  } else {
    return db.prepare(
      `SELECT * FROM cron_job_runs
       WHERE job_id = ?
       ORDER BY started_at DESC
       LIMIT ?`
    ).all(jobId, limit) as CronJobRun[];
  }
}

/**
 * Get all job runs across all jobs (recent)
 */
export async function getAllRecentRuns(limit = 50): Promise<CronJobRun[]> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query(
      `SELECT * FROM cron_job_runs
       ORDER BY started_at DESC
       LIMIT $1`,
      [limit]
    );
    return result.rows;
  } else {
    return db.prepare(
      `SELECT * FROM cron_job_runs
       ORDER BY started_at DESC
       LIMIT ?`
    ).all(limit) as CronJobRun[];
  }
}

// =============================================================================
// JOB CONFIG OPERATIONS
// =============================================================================

/**
 * Get job configuration
 */
export async function getJobConfig(jobId: string): Promise<CronJobConfig | null> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query(
      'SELECT * FROM cron_job_config WHERE job_id = $1',
      [jobId]
    );
    return result.rows[0] || null;
  } else {
    return db.prepare('SELECT * FROM cron_job_config WHERE job_id = ?').get(jobId) as CronJobConfig | null;
  }
}

/**
 * Get all job configurations
 */
export async function getAllJobConfigs(): Promise<CronJobConfig[]> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query('SELECT * FROM cron_job_config ORDER BY job_id');
    return result.rows;
  } else {
    return db.prepare('SELECT * FROM cron_job_config ORDER BY job_id').all() as CronJobConfig[];
  }
}

/**
 * Check if a job is enabled
 */
export async function isJobEnabled(jobId: string): Promise<boolean> {
  const config = await getJobConfig(jobId);
  return config?.enabled ?? true;
}

/**
 * Get jobs with consecutive failures exceeding threshold
 */
export async function getFailingJobs(threshold = 3): Promise<CronJobConfig[]> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query(
      `SELECT * FROM cron_job_config
       WHERE consecutive_failures >= $1 AND alert_on_failure = true
       ORDER BY consecutive_failures DESC`,
      [threshold]
    );
    return result.rows;
  } else {
    return db.prepare(
      `SELECT * FROM cron_job_config
       WHERE consecutive_failures >= ? AND alert_on_failure = 1
       ORDER BY consecutive_failures DESC`
    ).all(threshold) as CronJobConfig[];
  }
}

// =============================================================================
// CLEANUP
// =============================================================================

/**
 * Delete old job runs (older than retention days)
 */
export async function cleanupOldRuns(retentionDays = 30): Promise<number> {
  const db = getDb();
  
  if ('pool' in db) {
    const result = await db.query(
      `DELETE FROM cron_job_runs
       WHERE created_at < now() - interval '${retentionDays} days'`
    );
    const deleted = result.rowCount || 0;
    logger.info({ retentionDays, deleted }, 'Cleaned up old cron job runs');
    return deleted;
  } else {
    const result = db.prepare(
      `DELETE FROM cron_job_runs
       WHERE created_at < datetime('now', '-' || ? || ' days')`
    ).run(retentionDays);
    const deleted = result.changes;
    logger.info({ retentionDays, deleted }, 'Cleaned up old cron job runs');
    return deleted;
  }
}

